import React from 'react';
import BudgetHeader from './components/BudgetHeader';
import BudgetInput from './components/BudgetInput';
import BudgetOutput from './components/BudgetOutput';
class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            value: '/locales/uz/translation.json',
            languageData: "",
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
      }
    
      handleChange(event) {
        
        this.componentDidMount(this.setState({value: event.target.value}));
      }
    
      handleSubmit(event) {
        event.preventDefault();
       
      }
    componentDidMount() {
        setTimeout(() => {
        fetch(this.state.value)
        .then((r) => r.json())
        .then((data) =>{
            this.setState({languageData: data})
})
}, 100)

    }
    render() {
        return (
            <div>
                <div>
                
                    <select style={{width:"100px", borderStyle:"none", fontSize:"20px", outline:"none", position:"absolute", right:"20px", color:"white", backgroundColor:' rgba(228, 21, 21, 0.466)'}}
                    value={this.state.value} onChange={this.handleChange}>
                        <option value="/locales/uz/translation.json">Uzbek
                            </option>
                        <option value="/locales/en/translation.json">England</option>
                    </select>
                
                </div>
                <BudgetHeader allData={this.state.languageData}/>
                <BudgetInput allData={this.state.languageData}/>
                <BudgetOutput allData={this.state.languageData}/>
            </div>
        );
    }
}

export default App;