export const addToIncome = (description, moneyAmount) => {
    return {
        type: 'ADD_INCOME',
        description: description,
        moneyAmount: moneyAmount,
    }
};
export const addToExpense = (description, moneyAmount) => {
    return {
        type: 'ADD_EXPENSE',
        description: description,
        moneyAmount: moneyAmount,
    }
};
export const deleteIncome = (index) => {
    return {
        type: 'DELETE_INCOME',
        index:index
    }
};
export const deleteExpense = (index) => {
    return {
        type: 'DELETE_EXPENSE',
        index:index
    }
};

export const editIncomeDescription = (index, changedValue) => {
    return {
        type: 'EDIT_INC_DESCRIPTION',
        index: index,
        changedValue: changedValue
    }
};
export const editExpenseDescription = (index, changedValue) => {
    return {
        type: 'EDIT_EXP_DESCRIPTION',
        index: index,
        changedValue: changedValue
    }
};