import React from 'react';
import {connect} from 'react-redux';
import {addToIncome, addToExpense} from '../actions'
import '../Style/input.css';
class BudgetInput extends React.Component {
    constructor(prop) {
        super(prop);
        this.state = {
            type: "kirim",
            description:"",
            moneyAmount: "",
            };
    this.handleSubmit = this.handleSubmit.bind(this);
    }
    
handleSubmit(event) {
    event.preventDefault();
    if(this.state.description === ""||this.state.moneyAmount === ""){
        alert("Fill fields");
    } 
    else if (this.state.moneyAmount<=0){
        alert("Enter greater than 0");
    }
    else{
        if(this.state.type === 'kirim'){
            this.props.addToIncome(this.state.description, this.state.moneyAmount);
            this.setState({description:'', moneyAmount:""});
        }
        else{
        this.props.addToExpense(this.state.description, this.state.moneyAmount);
        this.setState({description:'', moneyAmount:""});
        }
        
    }
  }

    render() {
        return (
            <div className="top-input">
              <div style={{textAlign: "center"}}>
                  <form onSubmit={this.handleSubmit} style={{paddingTop:'20px'}}>
                    <select className='select-input'
                    value={this.state.type} 
                    onChange={e=> this.setState({type: e.target.value})}
                    >  
                        <option value="kirim">{this.props.allData.income}</option>
                        <option value="chiqim">{this.props.allData.expense}</option>
                    </select>
                    <input 
                    value={this.state.description} 
                    onChange={e=> this.setState({description: e.target.value})}
                    type="text" name="name" 
                    className="input-description"
                    />
                    <input 
                    className="input-money"
                    value={this.state.moneyAmount} 
                    onChange={e=> this.setState({moneyAmount: e.target.value})}
                    type="number" name="name" 
    
                     />
                    <button className="input-btn" type="submit">{this.props.allData.addBtn}
                    </button>
                  </form>
            
              </div>
            </div>
        );
    }
}

export default connect(null, {addToIncome,addToExpense}) (BudgetInput);