import React from 'react';
import {connect} from 'react-redux';
import {addToIncome, deleteIncome, deleteExpense, editIncomeDescription,editExpenseDescription} from '../actions';
import '../Style/output.css';
import Collapse from './Collapse';
class BudgetOutput extends React.Component {
    state = {
        breakpoint: window.innerWidth
    }
    editInc(index) {
       
        var changedValue = prompt("Edit or Change Description", this.props.editIncomes[index].description);
           if(changedValue !== ""&&changedValue!==null){
           this.props.editIncomeDescription(index, changedValue);
        }
    }
    editExp(index) {
        var changedValue = prompt("",this.props.editExpenses[index].description);
        if(changedValue !== ""&&changedValue!==null){
           this.props.editExpenseDescription(index, changedValue);
        }
    } 
    deleteInc(index){
        this.props.deleteIncome(index);
    }
    deleteExp(index){
        this.props.deleteExpense(index);
    }
    decideToTrancate(str){
        let x=this.state.breakpoint;
        switch(true){
            case x>1024: return this.trancateStr(str, 20);
            case x<=1024&&x>=721: return this.trancateStr(str, 30);
            case x<=720&&x>=511: return this.trancateStr(str, 18);
            case x<=510&&x>=411: return this.trancateStr(str, 7);
            case x<=410&&x>=311: return this.trancateStr(str, 2);
            case x<=310: return this.trancateStr(str, 0);
            default: return str;
        }
    }
    trancateStr(str, limit) {
        if(str.length<=limit){
            return str;
        }
        return str.slice(0, limit) + "...";
    }

    renderIncomeList() {
        return this.props.editIncomes? this.props.editIncomes.map((inc, index) => {
            return (
                
                <div style={{borderStyle:'outset',marginTop:"20px"}} key={index}>
        
                        <label className="des-opacity"
                        >{this.decideToTrancate(inc.description)}</label>
                        <button onClick={() => this.editInc(index)}
                        className='incEditBtn'
                        >{this.props.allData.editBtn}</button>
                        <div style={{ display:"inline-block", float:"right"}}>
            <label>{inc.moneyAmount}</label>
                            <button onClick={() => this.deleteInc(index)} 
                                               
                            style={{marginLeft:"5px"}}>
                                <img src='https://cdn.onlinewebfonts.com/svg/img_143168.png'
                                style={{width:"20px", height:"20px"}} alt="oddiy rasm  "
                                ></img>
                            </button>
                        </div>
                    </div>
                    
            );
        }):"";
    }
    renderExpenseList() {
        return this.props.editExpenses? this.props.editExpenses.map((exp, index) => {
            return (
                <div style={{borderStyle:'outset',marginTop:"20px"}} key={index}>
                <label style={{display:"inline-block"}}>{this.decideToTrancate(exp.description)}</label>
                <button onClick={() => this.editExp(index)}
                        className='incEditBtn'>{this.props.allData.editBtn}</button>
                <div style={{ display:"inline-block", float:"right"}}>
            <label>{exp.moneyAmount}</label>
                    <button onClick={() => this.deleteExp(index)} 
                    style={{marginLeft:"5px"}}>
                        <img style={{width:"20px", height:"20px"}} alt='rasm'
                        src="https://cdn.onlinewebfonts.com/svg/img_143168.png"></img>
                    </button>
                </div>
            </div>
            );
        }):"";
    }
    render() {
        return (
            <div className='output-top'>
                <div className='inc-div'>
                   
                    <Collapse title={this.props.allData.incomes}>
                    {this.renderIncomeList()}
                    </Collapse>
                </div>
                {/* <div style={{width:"20px"}}></div> */}
                <div className='exp-div'>
    
                    <Collapse title={this.props.allData.expenses}>
                   {this.renderExpenseList()}
                   </Collapse>
                </div>
            </div>
        );
    }
}
const mapStateToProps = state => {
    return state;
}

export default connect(mapStateToProps, {deleteIncome, deleteExpense, addToIncome, editIncomeDescription, editExpenseDescription}) (BudgetOutput);