import {combineReducers} from 'redux';
const incomeList = [];
const expenseList = [];

const editIncomes = (state=incomeList, action) => {
    switch(action.type){
        case 'ADD_INCOME': 
            let newArray = state.slice()
            newArray.splice(action.index, 0, {description: action.description, moneyAmount: action.moneyAmount})
            return newArray 
        case 'DELETE_INCOME': 
            let newArray1 = state.slice()
            newArray1.splice(action.index, 1)
            return newArray1
        case 'EDIT_INC_DESCRIPTION':
            let description= action.changedValue;
            return state.map((item, index) => {
                let moneyAmount = item.moneyAmount;
                let x = item.description;
                if(index !== action.index){
                    return item; 
                }
                return {
                    description, moneyAmount
                };
            })
        }
    return state;
}
const editExpenses = (state=expenseList, action) => {
    switch(action.type){
        case 'ADD_EXPENSE':  
            let newArray = state.slice()
            newArray.splice(action.index, 0, {description: action.description, moneyAmount: action.moneyAmount})
            return newArray 
        case 'DELETE_EXPENSE': 
            let newArray1 = state.slice()
            newArray1.splice(action.index, 1)
            return newArray1
        case 'EDIT_EXP_DESCRIPTION': 
         let description= action.changedValue;
         return state.map((item, index) => {
             let moneyAmount = item.moneyAmount;
             console.log(state);
             if(index !== action.index){
                 return item; 
             }
             return {
                 description, moneyAmount
             };
         })
    }
    return state; 
}
export default combineReducers({
    editIncomes,
    editExpenses,
});
