import React from 'react';
import '../Style/header.css';
import {connect} from 'react-redux';

class BudgetHeader extends React.Component {
    constructor() {
        super();
        var today = new Date(),
    date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
    this.state = {
        currentDate: date 
      }
    }
    allIncome(){
          const incMoney = this.props.editIncomes.reduce((total, i) => 
          total + parseInt(i.moneyAmount, 10), 0);
          return incMoney;
    }
    allExpense() {
        const expMoney = this.props.editExpenses.reduce((total, e) => 
        total + parseInt(e.moneyAmount, 10), 0);
        return expMoney;
    }
    render() {
        return (
            <div className="header-main-div">
            <div 
            style={{
                textAlign:"center",
                position:"absolute", 
            width:"400px",
            transform:"translate(-50%, -50%)", 
            left:"50%", marginTop:"100px",
            // backgroundColor:" rgba(33, 214, 124, 0.513)",
            color:"white"
            }}></div>
        <label className="budget-info">{this.props.allData.budgetStatus} { this.state.currentDate }: {this.allIncome()-this.allExpense()} $</label>
            
                <div>
                <img  alt="ok"
                className="header-img"
                src="https://www.tskbgd.com.tr/sites/1/upload/images/m/slider001-1.jpg" 
                width="100%" 
                height="500px"></img>
                </div>
                <div className="header-index">
                    <div className="header-income">
        <label className="header-label">{this.props.allData.income}</label>
        <label className="label-left">{this.allIncome()} $</label>
                    </div>
                    <div className="header-expense">
                        <label className="header-label">{this.props.allData.expense}</label>
        <label className="label-left">{this.allExpense()} $</label>
                    </div>
                </div>
            </div>
        );
    }
}
const mapStateToProps = state => {
    return state;
}

export default connect(mapStateToProps)(BudgetHeader);